@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Work Sans', sans-serif;
}

.container {
  position: relative;
  max-width: 600px;
  margin: 100px auto;
  border: 1px solid #343d46;
  border-radius: 5px;
}

.converter {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  max-width: 500px;
  margin: 0 auto;
  /* .converter_title */
  
  &_title {
    font-family: 'Roboto', sans-serif;
    margin: 35px 0 0 -15px;
    display: flex;
    font-size: 45px;
  }
  
  /* .converter_currencyBlock */
  
  &_currencyBlock {
    max-width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin: 30px 0 0 0;
  }
}
@media (max-width: 530px) {
  .converter{
    margin-top: 20px;
    &_currencyBlock {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    &_title{
      margin-left: 0;
      text-align: center;
      justify-content: center;
    }
  }
  .currency_search{
    margin-top: 10px;
  }
}

@media (max-width: 200px) {
  .converter{
    &_title{
      font-size: 30px;
    }
  }
}
#top{
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid rgb(85, 155, 241);;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom: none;
  background: rgb(85, 155, 241);
  width: 100%;
  height: 30px;
}
