.trade {

    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 10px;


    /* .trade_block */
    
    &_block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 20px;
        border-radius: 5px;
    }
    
    /* .trade_title */
    
    &_title {
        color: black;
        font-weight: bold;
        font-size: 20px;
    }
}
.tradeBlock_select{
    display: flex;
    flex-direction: column;
    align-items: center;
}
input{
    margin-top: 10px;
}
select{
    color:white;
    // border-radius: 3px;
    padding: 5px;
    background: rgb(143, 151, 161);
}
#swapImg{
    margin-top: 40px;
}

@media (max-width: 450px) {
    .trade{
        flex-direction: column;
        justify-content: space-between;
        padding: 5px 0;
    }
    .trade_block{
        padding: 10px 0;
    }
    .tradeBlock_select{
        flex-direction: row;
        justify-content: space-around;
    }
    #swapImg{
        margin: 0;
        padding: 0;
    }
}